* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  body {
    color: black;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
  
  /* Header */
  
  header {
    padding: 10px 30px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    /* position: -webkit-sticky; */
    position: sticky;
    top: 0;
    background-color: #efce39;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    background-position: 80%;
    z-index: 9999; 
  }
  
  header h2 {
    font-weight: bold;
    margin: 0;
    font-size: 36px;
    color: black;
  }
  
  header a {
    text-decoration: none;
    color: black;
  }
  
  header nav {
    margin: 7px 0;
  }
  
  header nav ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    list-style: none;
  }
  
  header nav ul li a {
    padding: 10px 15px;
    font-weight: lighter;
    font-size: 1.55vw;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  }
  
  header nav ul li a:hover {
    text-decoration: overline rgb(232, 106, 28);
    color: black;
  }
  
  
  /* End of header */
  
  
  
  /* Footer */
  footer {
    background: black;
    padding: 20px 35px;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
  
  footer h2 {
    color: #160457;
    font-size: 30px;
    margin: 0;
  }

  /* End of footer */

  section {
    padding: 60px;
  }
  
  /* Intro-starter */
  .starter {
    /* background-image: url("introbck.png"); */
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: flex-start;
    background-attachment: fixed;
    background-position: 100%;
  }
  
  .starter-text {
    width: 75%;
    text-align: left;
    margin: 3.5%;
    color: #fff;
    font-size: 18px;
    line-height: 1.2;
  }
  
  .starter-text h2 {
    font-style: italic;
    font-size: 55px;
    color: rgb(255, 115, 0);
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  }

  #btn {
    background-color:rgb(255, 115, 0)
  }

  .first-title {
    font-size: 48px;
    color: var(--secondary-color);
    padding-bottom: 20px;
    text-align: right;
    margin: 0 auto 35px auto;
    width: 50%;

  }
  
  /* About me */

  .about-me img {
    display: flex;
    flex-wrap: wrap;
    max-width: 275px;
  }

  .about-me p {
    display: flex;
    line-height: 1.7;
    font-size: 20px;
    text-align: center;
    flex: 1;
  }

  .me {
    margin: 50px auto;
    padding-bottom: 50px;
    display: flex;
    flex-wrap: wrap;
  }
  /* About me */
  
  .list-items {
    color:rgb(255, 115, 0)
  }

  .list-items:hover {
    color:rgb(255, 115, 0)

  }

  .other-title {
    color: black;
  }

  
  /* Projects sec end */
  
  
  /* Contact */

  .contact-me {
    background-color: #efce39;
    color: black;
    text-align: center;
  }
  
  
  .contact-info {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .contact-info > * {
    flex: 1;
    margin: 15px;
  }
  
  
  .contact-info div a {
    color: white;
  }
  
  .contact-info h3 {
    color: black;
    font-size: 32px;
  }

  .contact-info h4 {
    color: black;
  }
  
  .contact-info p, .contact-info address {
    margin: 20px 0;
    line-height: 1.5;
    font-size: 16px;
    font-style: normal;
  }
  
  .contact-info a {
    color: black;
  }
  
  
  @media screen and (max-width: 980px) {
    header {
      padding-bottom: 0;
      justify-content: center;
    }
  
    header h2 {
      width: 100%;
      text-align: center;
    }

    header nav ul {
      margin-top: 20px;
      width: 100%;
      justify-content: center;
    }
  
    header nav ul li a {
      font-size: 20px;
    }
  
    footer h2, footer div {
      text-align: center;
      width: 100%;
    }
  
    .starter-text { 
      width: 100%;
    }
    
    .starter-text {
      text-align: center;
    }
    
  }
  
  /* MEDIA QUERY FOR TABLETS AND SMALLER */
  @media screen and (max-width: 768px) {
    header {
      padding: 0px;
    }

    .about-me img {     
      flex: 0 32%;
      margin-right: 0;
      margin-top: 15px;
      margin-bottom: 15px;
    }

   .about-me p {
      flex: 100%;
      justify-content: center;
      text-align: center;
    }

    .me {
      flex: 100%;
      justify-content: center;
      text-align: center;

    }

    img {
      margin-top: 0;
      padding-top: 0;
    }

  
  }
  /* MEDIA QUERY FOR MOBILE PHONES AND SMALLER */
  @media screen and (max-width: 575px) {
    header {
      padding: 0px;
    }
  
    .intro p {
      width: 100%;
    }

    .contact-info {
      text-align: center;
    }
  
    .contact-info > * {
      flex: 0 100%;
      color: #fff;
    }
  
}
